import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"
import { MDBTypography, MDBRow, MDBCol, MDBCard, MDBCardBody, MDBCardHeader, MDBCardText, MDBBox } from "mdbreact";

const Intro = () => {
  const data = useStaticQuery(graphql`
  query {
    yaklass: file(relativePath: { eq: "YAKlass_logo_squere_w200.jpg" }) {
      childImageSharp {
        fluid {
          ...GatsbyImageSharpFluid
        }
      }
    }
    grgu: file(relativePath: { eq: "grodnenskiy-gosudarstvennyy-universitet-imeni-yanki-kupaly.png" }) {
      childImageSharp {
        fluid {
          ...GatsbyImageSharpFluid
        }
      }
    }
    mgiro: file(relativePath: { eq: "mgiro.png" }) {
      childImageSharp {
        fluid {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
`)

  return (
    <>
      <section>
        <MDBTypography tag="h2" variant="h2-responsive" className="text-center">
          Межрегиональная олимпиада по&nbsp;математике
      </MDBTypography>
        <MDBTypography tag="h3" variant="h3-responsive" className="text-center">
          среди учащихся III - IV классов учреждений общего среднего образования
      </MDBTypography>
        <MDBRow className="mt-4">
          <MDBCol lg="8">
            <MDBCard border="light" className="mb-3">
              <MDBCardHeader>Организатор Олимпиады</MDBCardHeader>
              <MDBCardBody>
                <MDBRow>
                  <MDBCol size="4" sm="2">
                    <Img fluid={data.yaklass.childImageSharp.fluid} />
                  </MDBCol>
                  <MDBCol size="8" sm="10">
                    <MDBCardText>
                      Образовательный интернет&nbsp;&mdash; портал &laquo;ЯКласс&raquo;
                  </MDBCardText>
                  </MDBCol>
                </MDBRow>
              </MDBCardBody>
            </MDBCard>
            <MDBCard border="light" className="mb-3">
              <MDBCardHeader>Партнеры Олимпиады</MDBCardHeader>
              <MDBCardBody>
                <MDBRow>
                  <MDBCol size="4" sm="2">
                    <Img fluid={data.mgiro.childImageSharp.fluid} />
                  </MDBCol>
                  <MDBCol size="8" sm="10">
                    <MDBCardText>ГУО &laquo;Минский городской институт развития образования&raquo;</MDBCardText>
                  </MDBCol>
                </MDBRow>
                <MDBRow className="mt-3">
                  <MDBCol size="4" sm="2">
                    <Img fluid={data.grgu.childImageSharp.fluid} />
                  </MDBCol>
                  <MDBCol size="8" sm="10">
                    <MDBCardText>УО &laquo;Гродненский государственный университет имени Янки Купалы&raquo;</MDBCardText>
                  </MDBCol>
                </MDBRow>
              </MDBCardBody>
            </MDBCard>
          </MDBCol>
          <MDBCol lg="4">
            <MDBBox className="p-3 authors">
              <p><b>Автор-разработчик заданий:</b> Т.А. Полюхович, бакалавр педагогических наук, учитель начальных классов
                  ГУО &laquo;Средняя школа &#8470;&nbsp;115&nbsp;г. Минска&raquo;
            </p>
              <p className="m-0">
                <b>Рецензент:</b> Д.И. Прохоров, кандидат педагогических наук, доцент кафедры педагогики, заместитель
                декана факультета повышения квалификации педагогических работников государственного учреждения образования
                &laquo;Минский городской институт развития образования&raquo;
            </p>
            </MDBBox>
          </MDBCol>
        </MDBRow>
      </section>
    </>
  )
}

export default Intro