import React from 'react';
import PayBtn from '../PayBtn/payBtn';
import Modal from "../Modal/modal";
import { MDBJumbotron, MDBCardBody, MDBCardTitle, MDBInput, MDBAlert, MDBIcon } from "mdbreact";

const Jumbotron = () => {
  const checkBox = React.createRef();

  return (
    <>
      <section>
        <MDBJumbotron className="text-center mx-auto p-3 my-5">
          <MDBCardBody>
            <MDBCardTitle>Регистрация и&nbsp;оплата участия</MDBCardTitle>
            <p className="default-color-text my-4 font-weight-bold font-s-def">Участие в&nbsp;Олимпиаде&nbsp;&mdash; добровольное и&nbsp;платное</p>
            <p className="font-s-def">Стоимость участия в Олимпиаде - 3 бел. рубля</p>
            <p className="font-s-def">Для участия в&nbsp;Олимпиаде необходимо ознакомится с&nbsp;Инструкцией</p>
            <MDBAlert color="warning" >
              <MDBIcon icon="bell" className="mr-3" /> После оплаты Вы будете переадресованы на стартницу с доступом к заданиям по Олимпиаде. Сохраните эту страницу в закладки, что бы не потерять доступ к ней в дальнейшем.
            </MDBAlert>
            <hr className="my-4" />
            <div className="pt-2">
              <Modal />
              <PayBtn checkBox={checkBox} />
            </div>
            <div className="custom-input-checkbox mt-3">
              <MDBInput label="С условиями проведения Олимпиады согласен" filled type="checkbox" id="checkbox2" ref={checkBox} />
            </div>
          </MDBCardBody>
        </MDBJumbotron>
      </section>
    </>
  )
}

export default Jumbotron