import React from "react";
import { useStaticQuery, graphql } from "gatsby";
import Img from "gatsby-image";
import { MDBRow, MDBCol } from "mdbreact";

const Doc = () => {
  const data = useStaticQuery(graphql`
    query {
      login: file(relativePath: { eq: "login.jpg" }) {
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid
          }
        }
      }
      registration: file(relativePath: { eq: "registration.jpg" }) {
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid
          }
        }
      }
      olympiadRegistration: file(relativePath: { eq: "olympiad_registration.png" }) {
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid
         }
        }
      }
    }
  `)

  return (
    <>
      <div className="text-left doc-wrapper">
        <MDBRow>
          <MDBCol className="col-doc">1</MDBCol>
          <MDBCol size="11">Для участия в Олимпиаде участнику необходимо быть зарегистрированным на сайте ЯКласс (yaklass.by) и иметь свой профиль. В профиле должны быть       указаны полные и подлинные имя и фамилия, действующий адрес электронной почты участника, а также контактный номер мобильного телефона, по которому можно связаться с участником. Вход через иные сервисы, в том числе schools.by и социальные сети, не является подтверждением наличия профиля участника Олимпиады.
          </MDBCol>
        </MDBRow>
        <MDBRow>
          <MDBCol className="col-doc">2</MDBCol>
          <MDBCol size="11">Проверить регистрацию на сайте и свой профиль можно по этой <a href="https://www.yaklass.by/Account/Login" target="_blank" rel="noopener noreferrer">ссылке</a> зайдя на портал под своим логином и паролем.
          <Img fluid={data.login.childImageSharp.fluid} />
          </MDBCol>
        </MDBRow>
        <MDBRow>
          <MDBCol className="col-doc">3</MDBCol>
          <MDBCol size="11">При отсутствии профиля участнику Олимпиады необходимо пройти регистрацию на сайте ЯКласс (yaklass.by) по ссылке <a href="https://www.yaklass.by/Account/Register?returnUrl=%2FAccount%2FLogin" target="_blank" rel="noopener noreferrer">ссылке</a> обязательно указав свою роль на портале – Ученик, электронную почту, полные и настоящие имя и фамилию, контактный номер мобильного телефона, а также выбрать школу и класс. В случае отсутствия школы в списке нажать на меню «Добавить новую школу» и ввести полное название своей школы и полный адрес.
          <Img fluid={data.registration.childImageSharp.fluid} />
          </MDBCol>
        </MDBRow>
        <MDBRow>
          <MDBCol className="col-doc">4</MDBCol>
          <MDBCol size="11">Зарегистрировавшись на сайте и получив подтверждение регистрации на электронную почту, необходимо <b>оплатить</b> участие в Олимпиаде.<br></br>
            <span class="badge badge-pill badge-warning">Внимание</span><br></br>
            <b>Не переходите к оплате, если Вы еще не зарегистрировались на портале &laquo;Якласс&raquo;</b><br></br>
            <b>При оплате необходимо в строке &laquo;Фамилия&raquo; и &laquo;Имя&raquo; указать фамилию и имя ребенка, который будет принимать участие в Олимпиаде. Эти данные должны совпадать с данными регистрации на сайте yaklass.by</b></MDBCol>
        </MDBRow>
        <MDBRow>
          <MDBCol className="col-doc">5</MDBCol>
          <MDBCol size="11">После оплаты Вы будете переадресованы на страницу записи для участия в Олимпиаде.<br></br>
            <span class="badge badge-pill badge-warning">Внимание</span><br></br>
            <b>Вам необходимо сохранить эту страницу в закладки, чтобы иметь доступ к Одимпиадным задачам.</b>
            <Img fluid={data.olympiadRegistration.childImageSharp.fluid} />
          </MDBCol>
        </MDBRow>
        <MDBRow>
          <MDBCol className="col-doc">6</MDBCol>
          <MDBCol size="11">Задания Олимпиады в&nbsp;виде проверочной работы будут доступны для решения участникам Олимпиады, зарегистрировавшимся на&nbsp;интернет&nbsp;&mdash; портале &laquo;ЯКласс&raquo; и&nbsp;оплатившим свое участие в&nbsp;Олимпиаде, 28&nbsp;марта 2020 года с&nbsp;11.00 часов до&nbsp;12.00 часов этого&nbsp;же дня.</MDBCol>
        </MDBRow>
        <MDBRow>
          <MDBCol className="col-doc">7</MDBCol>
          <MDBCol size="11">Задания Олимпиады можно решать в&nbsp;любой последовательности. При выполнении заданий Олимпиады рекомендуется пройти &laquo;Гимнастику для глаз&raquo;, за&nbsp;что начисляются дополнительные баллы.
          </MDBCol>
        </MDBRow>
        <MDBRow>
          <MDBCol className="col-doc">8</MDBCol>
          <MDBCol size="11">Критериями определения победителей Олимпиады&nbsp;&mdash; наибольшее количество набранных баллов и&nbsp;минимальное время, за&nbsp;которое участник решил задания Олимпиады.
          </MDBCol>
        </MDBRow>
        <MDBRow>
          <MDBCol className="col-doc">9</MDBCol>
          <MDBCol size="11">Неоплата участия в&nbsp;Олимпиаде, а&nbsp;так&nbsp;же иные несоответствия, в&nbsp;том числе данных, указанных в&nbsp;профиле участника Олимпиады, являются основанием для дисквалификации.
          </MDBCol>
        </MDBRow>
      </div>
    </>
  )
}

export default Doc;
