import React, { useState } from 'react';
import { MDBBtn, MDBModal, MDBModalBody, MDBModalHeader, MDBModalFooter, MDBIcon } from 'mdbreact';
import Doc from './content';

const Modal = () => {
  const [isOpen, toggle] = useState(false);

  const onModal = () => {
    toggle(!isOpen);
  }

  return (
    <>
      <MDBBtn onClick={onModal} outline>Инструкция <MDBIcon icon="info-circle" /></MDBBtn>
      <MDBModal isOpen={isOpen} toggle={onModal} className="custom-modal">
        <MDBModalHeader toggle={onModal} className="text-left">
          ИНСТРУКЦИЯ<br></br>
          О порядке участия в Межрегиональной Олимпиаде по математике<br></br>
          среди учащихся III – IV классов учреждений общего и среднего образования (далее- Олимпиада)
        </MDBModalHeader>
        <MDBModalBody>
          <Doc />
        </MDBModalBody>
        <MDBModalFooter>
          <MDBBtn outline onClick={onModal}>Закрыть</MDBBtn>
        </MDBModalFooter>
      </MDBModal>
    </>
  );
}

export default Modal
