import React, { useState } from 'react';
import { MDBBtn, MDBModal, MDBModalBody, MDBModalFooter, MDBIcon } from 'mdbreact';

const PayBtn = ({ checkBox }) => {
  const [isOpen, toggle] = useState(false);

  const onPay = () => {
    const isCheck = checkBox.current.inputElementRef.current.checked;
    if (isCheck) {
      window.location = 'https://checkout.bepaid.by/v2/confirm_order/prd_fbb3a61f35993b1f/9435';
    } else {
      onModal();
    }
  }

  const onModal = () => {
    toggle(!isOpen);
  }

  return (
    <>
      <MDBBtn onClick={onPay}>Оплатить <MDBIcon far icon="credit-card" /></MDBBtn>
      <MDBModal isOpen={isOpen} toggle={onModal} className="custom-modal modal-dialog-centered modal-sm">
        <MDBModalBody>
          <span className="font-weight-bold red-text">Необходимо прочитать инструкцию и подтвердить согласие с условиями Олимпиады</span>
        </MDBModalBody>
        <MDBModalFooter>
          <MDBBtn outline onClick={onModal}>Закрыть</MDBBtn>
        </MDBModalFooter>
      </MDBModal>
    </>
  )
}

export default PayBtn;